import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss'

let 박스 = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font-size : 25px;
  color : ${props => props.색상 }
`;

function Detail(props) {
    
    let [onOff, onOffChange] = useState(true);
    let [userInput, userInputChange] = useState();

    useEffect(()=>{
      let timer = window.setTimeout(()=>{
        onOffChange(false);
        console.log('안녕');
      }, 2000);

      return ()=>{ clearTimeout(timer) }
    },[]);

    let { id } = useParams();
    let history = useHistory();
    

    return(
      <div className="container">
        <박스>
          <제목 색상='blue'>상세페이지</제목>
          <제목 className="red">상세페이지</제목>
        </박스>

        {userInput}
        <input onChange={(e)=>{userInputChange(e.target.value)}}></input>

        {
          onOff == true
          ? <div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다</p>
            </div>
          : null
        }
        
        <div className="row">
          <div className="col-md-6">
            <img src={"https://codingapple1.github.io/shop/shoes" + (props.shoes.find(x => x.id == id).id + 1) + ".jpg"} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{
              props.shoes.find(x => x.id == id).title
            }</h4>
            <p>{props.shoes.find(x => x.id == id).content}</p>
            <p>{props.shoes.find(x => x.id == id).price}원</p>
            <Info stock={props.stock} />
            <button className="btn btn-danger" onClick={()=>{ props.stockChange([9,11,12]) }}>주문하기</button> 
            <button className="btn btn-danger" onClick={()=>{ history.goBack(); }}>뒤로가기</button> 
          </div>
        </div>
      </div> 
    )
}

function Info(props){
  return(
    <p>재고 : {props.stock[0]}</p>
  )
}

export default Detail;
